* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: radial-gradient(73% 147%, #e6d4d4 59%, #eeded9 100%),
    radial-gradient(
      91% 146%,
      rgba(255, 255, 255, 0.5) 47%,
      rgba(0, 0, 0, 0.5) 100%
    );
  background-blend-mode: screen;
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  position: relative;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 100px;
}

input {
  padding: 0.8rem;
  margin-left: 10px;
  border: none;
  font-size: 1rem;
  background: white;
}

.inputbox {
  min-width: 25vw;
  grid-area: inputbox;
}

input:focus {
  outline: none;
}

.datalist-box {
  max-width: 150px;
  grid-area: datalistbox;
}

.datalist-box::placeholder {
  font-size: 0.8rem;
}

.plus-btn {
  background: white;
  color: #51a872;
  cursor: pointer;
  padding: 0.2rem;
  font-size: 30px;
  margin-left: 10px;
  transition: all 1s ease;
  grid-area: plusbtn;
}
.plus-btn:hover {
  background: #51a872;
  color: white;
}
button {
  background: inherit;
  border: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}

.folder-container {
  display: flex;
  justify-content: center;
  padding-top: 20px;
}

.folderandtext {
  padding: 20px;
  margin-left: 10px;
}

.foldericon {
  color: #f3d26f;
  font-size: 35px;
  padding-bottom: 10px;
  transition: all 0.5s ease;
}

.foldericon:hover {
  color: #f8d775b6;
}

.foldertext {
  color: black;
  transition: all 1s ease;
}

.foldertext:hover {
  text-decoration: underline;
}

.container {
  text-align: center;
  margin: 0 auto;
  width: 40%;
}

.copy {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  margin-top: 0.5rem;
  padding-left: 0.5rem;
}

.trash-btn {
  font-size: 20px;
  background: rgb(207, 37, 37);
  color: white;
  padding: 0.5rem;
  transition: all 1s ease;
}

.trash-btn:hover {
  background: white;
  color: rgb(207, 37, 37);
}

.analysis-btn {
  /* color: #f3b860; */
  color: #975563d7;
  margin-left: 20px;
  font-size: 40px;
  transition: all 1s ease;
}

.analysis-btn:hover {
  color: #80495573;
}

.analysis-btnopen {
  color: #80495573;
}

.duplicate {
  max-width: fit-content;
  text-align: left;
  margin: 10px auto;
  color: #8d8c8c;
}

.duplicatecounter {
  display: flex;
  max-width: 40%;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding-left: 0.5rem;
  text-align: center;
  margin: 0.5rem auto;
}

.replicateparagraph {
  font-size: 12px;
  color: black;
  margin: 10px;
}

.replicateparagraph:hover {
  text-decoration: underline;
}

.nb {
  text-align: center;
  padding-top: 20px;
  color: #8d8c8c;
}

.formcontainer {
  background-color: #e6dad6;
  width: 30vw;
  min-height: 70vh;
  margin: 0 auto;
}

.signinform {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  min-height: 60vh;
  padding-top: 50px;
}

.signinput {
  width: 300px;
  background: none;
  border-bottom: 1px solid rgb(155, 144, 144);
}

.signinbtn {
  background-color: #975563ad;
  font-size: 15px;
  padding: 10px;
  min-width: 300px;
  border-radius: 50px;
  margin-top: 10px;
  transition: all 1s ease;
}

.signinbtn:hover {
  background-color: #975563f5;
}

.dropdown {
  max-width: 100px;
  padding: 0.8rem;
  border: none;
  font-size: 0.8rem;
  margin-left: 10px;
  grid-area: dropdown;
}

.dropdown:focus {
  outline: none;
}

.signoutbtn {
  background: rgb(235, 94, 94);
  color: white;
  font-size: 15px;
  padding: 10px;
  border-radius: 5%;
  width: fit-content;
  position: fixed;
  left: 90%;
  top: 5%;
  transition: all 1s ease;
}

.signoutbtn:hover {
  background: rgb(202, 29, 29);
}

.signintext {
  font-size: 18px;
  font-weight: 700;
}

.signinbottom {
  font-size: 15px;
  max-width: 200px;
  margin-top: 10px;
}

@media screen and (max-width: 900px) and (min-width: 300px) {
  .formcontainer {
    width: 70vw;
  }

  .signinput {
    width: 200px;
    margin-top: 10px;
  }

  .signinbtn {
    min-width: 200px;
  }

  .nb {
    padding-top: 65px;
  }

  .signoutbtn {
    top: 3%;
    left: 77%;
  }

  .container {
    max-width: 70%;
  }

  form {
    display: grid;
    grid-template-areas:
      "inputbox dropdown"
      "datalistbox plusbtn";
    row-gap: 10px;
    padding-top: 50px;
  }
  .datalist-box {
    max-width: 250px;
  }

  .datalist-box::placeholder {
    font-size: 1rem;
  }

  .duplicate {
    text-align: center;
  }

  .container {
    width: 70%;
  }
  .duplicatecounter {
    max-width: 70%;
  }
}
